  font-weight: 500;
  font-style: normal;
  font-display: swap; }

.html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "Oswald";
  font-size: 14px;
  line-height: 18px;
  font-weight: 400px;
  color: #444444;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

p {
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

a {
  text-decoration: none; }

h1, h2, h3 {
  margin: 0;
  padding: 0; }

.container {
  width: 320px;
  margin: 0 auto; }

@media (min-width: 768px) {
  .container {
    width: 660px;
    margin: 0 29px; } }

@media (min-width: 1200px) {
