.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header__logo-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 12px 20px;
  text-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border: 1px #E6E6E6 solid; }

.header__logo-block {
  margin-right: auto; }

.header__top-item {
  padding: 22px 69px;
  text-align: center;
  border: 1px #E6E6E6 solid;
  font-family: "Oswald";
  font-weight: 500px;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase; }

.header__top-item--main {
  padding: 22px 122px; }

.main-menu__link {
  color: #444444; }

.header__description {
  background: url(../img/background-mobile.jpg);
  height: 302px;
  margin-bottom: 138px;
  position: relative; }

.header__description-title {
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  color: #ffffff;
  font-style: normal;
  font-weight: 400;
  margin-bottom: 25px;
  padding-top: 27px; }

.header__description-text {
  font-style: normal;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff; }

.header__description-link {
  display: block;
  background-color: #68b738;
  color: #ffffff;
  margin: 0 20px;
  padding: 8px 0;
  text-align: center; }

.header__description::before {
  content: "";
  position: absolute;
  background-image: url(../img/bucket-mobile.png);
  width: 280px;
  height: 270px;
  left: 20px;
  top: 168px; }

.main-menu--hide {
  display: none; }

.header__burger {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  width: 29px;
  height: 29px;
  position: relative; }

.header__burger--open::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 1.5px;
  background-color: #444444;
  -webkit-box-shadow: 0 8px #444444, 0 16px #444444;
          box-shadow: 0 8px #444444, 0 16px #444444;
  top: 8px;
  left: 2px; }

.header__burger--close::before {
  position: absolute;
  content: "";
  width: 23px;
  height: 1.5px;
  background-color: #444444;
  -webkit-transform: matrix(0.71, -0.75, 0.67, 0.71, 0, 0);
          transform: matrix(0.71, -0.75, 0.67, 0.71, 0, 0);
  top: 11px;
  left: 3px; }

.header__burger--close::after {
  position: absolute;
  content: "";
  width: 23px;
  height: 1.5px;
  background-color: #444444;
  -webkit-transform: matrix(0.71, 0.75, -0.67, 0.71, 0, 0);
          transform: matrix(0.71, 0.75, -0.67, 0.71, 0, 0);
  top: 11px;
  right: 3px; }

@media (min-width: 768px) {
  .header__description {
    background: none; }
  .main-menu--hide {
    display: block; }
  .header__burger {
    display: none; }
  .header__top-item {
    padding: 0;
    border: none;
    margin-right: 33px;
    padding-top: 24px; }
  .header__top-item:last-child {
    margin: 0; }
  .main-menu__list {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex; }
  .header__logo-wrapper {
    padding: 0;
    border: none;
    width: 174px;
    margin-right: 90px; }
  .header__top {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    width: 100%; } }
